import { Component, OnInit } from '@angular/core';
import { OperatorsItem, OperatorsService } from '../../../services/operators.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
    selector: 'app-edit-page',
    templateUrl: './edit-page.component.html',
    styleUrls: ['./edit-page.component.scss']
})
export class EditPageComponent implements OnInit {
    form: OperatorsItem = {
        id: null,
        pid: null,
        name: '',
        value: '',
        permission_id: null
    };
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private opServ: OperatorsService
    ) {
        const id = this.route.snapshot.params.id;
        const pid = this.route.snapshot.queryParams.pid;
        if (id) {
            this.opServ.fetchById(id).subscribe(res => {
                this.form = res;
            });
        }
        if (pid) {
            this.form.pid = pid;
        }
    }

    ngOnInit() {
    }
    onSubmit() {
        const { form, opServ } = this;
        if (form.id) {
            opServ.updateById(form.id, form).subscribe(res => {
                this.backListPage();
            });
        } else {
            opServ.create(form).subscribe(res => {
                this.backListPage();
            });
        }

    }
    backListPage() {
        this.router.navigate(['operators']);
    }

}
