import { Component, OnInit } from '@angular/core';
import { RoleItem, RolesService } from '../../../services/roles.service';
import { UserItem, UsersService } from '../../../services/users.service';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
    selector: 'app-role-page',
    templateUrl: './role-page.component.html',
    styleUrls: ['./role-page.component.scss']
})
export class RolePageComponent implements OnInit {
    userInfo: UserItem = null;
    roleLsit: RoleItem[] = [];
    selectedValues: number[] = [];
    constructor(
        private router: Router,
        private rolesServ: RolesService,
        private usersServ: UsersService,
        private route: ActivatedRoute
    ) {
        let userId = this.route.snapshot.params.id;
        // 
        this.usersServ.fetchById(userId).subscribe(res => this.userInfo = res);
        // 
        this.rolesServ.fetchAll().subscribe(res => this.roleLsit = res);
        // 
        this.usersServ.fetchUserRoles(userId).subscribe(res => this.selectedValues = res);
    }

    ngOnInit() {

    }
    onSave() {
        this.usersServ.updateUserRoles(
            this.userInfo.id,
            this.selectedValues
        ).subscribe(res => {
            if (res) {
                this.router.navigate(['users']);
            }
        });
    }
    onChange(roleId: number) {
        const values = this.selectedValues;
        if (values.includes(roleId)) {
            this.selectedValues = values.filter(e => e != roleId);
        } else {
            values.push(roleId);
        }
    }
}
