import { Component, OnInit } from '@angular/core';
import { UserItem, UsersService } from '../../../services/users.service';
@Component({
    selector: 'app-list-page',
    templateUrl: './list-page.component.html',
    styleUrls: ['./list-page.component.scss']
})
export class ListPageComponent implements OnInit {
    userList: UserItem[] = [];
    constructor(
        private userServ: UsersService
    ) {
        this.userServ.fetchAll().subscribe(res => this.userList = res);
    }

    ngOnInit() {
    }
    onDeleteById(id: number | string) {
        this.userServ.deleteById(id).subscribe(res1 => {
            this.userServ.fetchAll().subscribe(res2 => this.userList = res2);
        });
    }

}
