import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class RolesService {

    constructor(
        private http: HttpClient
    ) { }

    fetchAll(): Observable<RoleItem[]> {
        return this.http.get('/api/roles') as Observable<RoleItem[]>;
    }
    fetchById(id: number | string): Observable<RoleItem> {
        return this.http.get(`/api/roles/${id}`) as Observable<RoleItem>;
    }
    updateById(id: string | number, doc: RoleItem): Observable<any> {
        return this.http.put(`/api/roles/${id}`, doc);
    }
    create(doc: RoleItem): Observable<any> {
        return this.http.post('/api/roles', doc);
    }
    deleteById(id: number | string): Observable<any> {
        return this.http.delete('/api/roles/' + id);
    }


    fetchRolePermission(roleId: number | string): Observable<number[]> {
        return this.http.get('/api/roles/' + roleId + '/permissions') as Observable<number[]>;
    }
    updateRolePermission(roleId: number | string, permissionIds: (number | string)[]): Observable<any> {
        return this.http.put('/api/roles/' + roleId + '/permissions', {
            permissionIds
        });
    }


}


export class RoleItem {
    id: number;
    pid: number;
    name: string;
}
