import { Component, OnInit } from '@angular/core';
import { OperatorsItem, OperatorsService } from '../../../services/operators.service';
@Component({
    selector: 'app-list-page',
    templateUrl: './list-page.component.html',
    styleUrls: ['./list-page.component.scss']
})
export class ListPageComponent implements OnInit {
    opList: OperatorsItem[] = [];
    constructor(
        private opServe: OperatorsService
    ) {
        this.opServe.fetchAll().subscribe(res => {
            this.opList = res;
        });
    }

    ngOnInit() {
    }
    onDeleteById(id: string | number) {
        this.opServe.deleteById(id).subscribe(res1 => {
            this.opServe.fetchAll().subscribe(res2 => {
                this.opList = res2;
            });
        });
    }
}
