import { Component, OnInit } from '@angular/core';
import { PartnerItem, PartnersService } from '../../../services/partners.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
    selector: 'app-edit-page',
    templateUrl: './edit-page.component.html',
    styleUrls: ['./edit-page.component.scss']
})
export class EditPageComponent implements OnInit {

    form: PartnerItem = {
        id: null,
        pid: null,
        name: '',
        value: '',
        attr: null,
        permission_id: null
    };
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private partnersServe: PartnersService
    ) {
        const id = this.route.snapshot.params.id;
        const pid = this.route.snapshot.queryParams.pid;
        if (id) {
            this.partnersServe.fetchById(id).subscribe(res => {
                this.form = res;
            });
        }
        if (pid) {
            this.form.pid = pid;
        }
    }

    ngOnInit() {
    }
    onSubmit() {
        const { form, partnersServe } = this;
        if (!form.id) {
            partnersServe.create(form).subscribe(res => {
                this.toListPage();
            });
        } else {
            partnersServe.updateById(form.id, form).subscribe(res => {
                this.toListPage();
            });
        }

    }
    private toListPage() {
        this.router.navigate(['partners']);
    }
}
