import { Component, OnInit } from '@angular/core';
import { OperatorsItem, OperatorsService } from '../../../services/operators.service';
import { PartnerItem, PartnersService } from '../../../services/partners.service';
import { RolesService } from '../../../services/roles.service';
import { forkJoin, of } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import { isChildTree } from '../../../shared/util/listToTree';
@Component({
    selector: 'app-edit-permission-page',
    templateUrl: './edit-permission-page.component.html',
    styleUrls: ['./edit-permission-page.component.scss']
})
export class EditPermissionPageComponent implements OnInit {
    // 
    roleInfo: { id: number, name: string } = null;

    // 
    operatorList: OperatorsItem[] = [];
    partnerList: PartnerItem[] = [];
    // 
    selectedValues: number[] = [];
    constructor(
        private opServ: OperatorsService,
        private pertnersServ: PartnersService,
        private rolesServ: RolesService,
        private route: ActivatedRoute,

    ) {
        const roleId = route.snapshot.params.id;
        // 
        this.rolesServ.fetchById(roleId).pipe(
            switchMap(res => {
                this.roleInfo = res;
                return forkJoin([
                    // 
                    this.opServ.fetchAll(),
                    this.pertnersServ.fetchAll(),
                    // 
                    res.pid ? this.rolesServ.fetchRolePermission(res.pid) : of(false),
                    // 
                    this.rolesServ.fetchRolePermission(roleId)
                ]);
            })
        ).subscribe(res => {
            this.operatorList = res[0].filter(e => {
                if (Array.isArray(res[2])) {
                    return res[2].includes(e.permission_id);
                } else {
                    return true;
                }
            });
            this.partnerList = res[1].filter(e => {
                if (Array.isArray(res[2])) {
                    return res[2].includes(e.permission_id);
                } else {
                    return true;
                }
            });
            this.selectedValues = res[3];
        });
    }

    ngOnInit() {
    }
    onSave() {
        const { partnerList, operatorList } = this;
        // 
        const a = [];
        const b = [];
        this.selectedValues.forEach(v => {
            const has1 = partnerList.find(e => e.permission_id === v);
            const has2 = operatorList.find(e => e.permission_id === v);
            if (has1) {
                a.push(has1);
            }
            if (has2) {
                b.push(has2);
            }
        });

        if (!isChildTree(partnerList, a) || !isChildTree(operatorList, b)) {
            return alert('');
        }
        this.rolesServ.updateRolePermission(this.roleInfo.id, this.selectedValues).subscribe({
            next: () => alert(''),
            error: () => alert('')
        });
    }
    onChange(value: string) {
        const val = parseInt(value, 10);
        if (this.selectedValues.includes(val)) {
            this.selectedValues = this.selectedValues.filter(e => e !== val);
        } else {
            this.selectedValues.push(val);
        }
    }

}

