import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { tap, map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class SessService {
    // 
    sess: Sess = null;
    constructor(
        private http: HttpClient
    ) { }
    getSess(): Observable<Sess> {
        return this.http.get('/api/sess').pipe(
            map((res: Res) => resToSess(res)),
            tap(res => {
                this.sess = res;
            })
        );
    }
    login(data: { email: string, pwd: string }): Observable<Sess> {
        return this.http.post('/api/sess', data).pipe(
            map((res: Res) => resToSess(res)),
            tap(res => {
                this.sess = res;
            })
        );
    }
    logout(): Observable<boolean> {
        const ob = this.http.delete('/api/sess') as Observable<boolean>;
        return ob.pipe(
            tap(res => {
                if (res) {
                    this.sess = null;
                }
            })
        );
    }

}

class Res {
    user: {
        id: number;
        name: string;
        email: string;
    };
    perms: {
        perm_type: 'operator' | 'partner',
        value: string
    }[];
}

export class Sess {
    id: number;
    name: string;
    email: string;
    perms: {
        partner: string[];
        operator: string[];
    };
}

function resToSess(res: Res): Sess {
    return {
        id: res.user.id,
        name: res.user.name,
        email: res.user.email,
        perms: {
            partner: res.perms.filter(e => e.perm_type === 'partner').map(e => e.value),
            operator: res.perms.filter(e => e.perm_type === 'operator').map(e => e.value)
        }
    };
}
