import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class UsersService {

    constructor(
        private http: HttpClient
    ) { }

    fetchAll(): Observable<UserItem[]> {
        return this.http.get('/api/users') as Observable<UserItem[]>;
    }
    fetchById(uid: number | string): Observable<UserItem> {
        return this.http.get(`/api/users/${uid}`) as Observable<UserItem>;
    }
    updateById(id: string | number, doc: UserItem): Observable<any> {
        return this.http.put('/api/users/' + id, doc);
    }
    create(doc: UserItem): Observable<any> {
        return this.http.post('/api/users', doc);
    }
    deleteById(uid: number | string): Observable<any> {
        return this.http.delete('/api/users/' + uid);
    }
    updateUserRoles(userId: number, roleIds: number[]): Observable<boolean> {
        return this.http.put(`/api/users/${userId}/roles`, { roleIds }) as Observable<boolean>;
    }
    fetchUserRoles(userId: number): Observable<number[]> {
        return this.http.get(`/api/users/${userId}/roles`) as Observable<number[]>;
    }
}


export class UserItem {
    id: number;
    name: string;
    email: string;
    pwd?: string;
    roles?: string;
}
