import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class PartnersService {

    constructor(
        private http: HttpClient
    ) { }

    fetchAll(): Observable<PartnerItem[]> {
        return this.http.get('/api/partners') as Observable<PartnerItem[]>;
    }
    fetchById(id: number | string): Observable<PartnerItem> {
        return this.http.get(`/api/partners/${id}`) as Observable<PartnerItem>;
    }
    updateById(id: string | number, doc: PartnerItem): Observable<any> {
        return this.http.put('/api/partners/' + id, doc);
    }
    create(doc: PartnerItem): Observable<any> {
        return this.http.post('/api/partners', doc);
    }
    deleteById(id: number | string): Observable<any> {
        return this.http.delete('/api/partners/' + id);
    }
}


export class PartnerItem {
    id: number;
    pid: number;
    name: string;
    value: string;
    attr: string;
    permission_id: number;
}
