interface IListNode {
    id: number;
    pid: number;
    [attr: string]: any;
}
interface ITreeNode extends IListNode {
    children: ITreeNode[];
}

function listToTree<T1 extends IListNode>(list: T1[], id: number) {
    return list.filter(e => e.pid === id).map(e => {
        return { ...e, children: listToTree(list, e.id) };
    });
}
// 
function isChildTree(parentList: IListNode[], childList: IListNode[]): boolean {
    return childList.every(node => {
        // 1. 
        const hasNode = parentList.find(e => e.id === node.id);
        if (!hasNode) {
            return false;
        }
        // 2. ,
        const pnode = parentList.find(e => e.id === hasNode.pid);
        if (pnode) {
            const hasNode2 = childList.find(e => e.id === pnode.id);
            if (!hasNode2) {
                return false;
            }
        }
        return true;
    });
}

export { listToTree, isChildTree };

