import { Component, OnInit } from '@angular/core';
import { RoleItem, RolesService } from '../../../services/roles.service';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
    selector: 'app-edit-page',
    templateUrl: './edit-page.component.html',
    styleUrls: ['./edit-page.component.scss']
})
export class EditPageComponent implements OnInit {
    form: RoleItem = {
        id: null,
        pid: null,
        name: ''
    };
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private rolesServ: RolesService
    ) {
        const id = this.route.snapshot.params.id;
        const pid = this.route.snapshot.queryParams.pid;
        if (id) {
            this.rolesServ.fetchById(id).subscribe(res => {
                this.form = res;
            });
        }
        if (pid) {
            this.form.pid = pid;
        }
    }

    ngOnInit() {
    }
    onSubmit() {
        const { form, rolesServ } = this;
        if (!form.id) {
            rolesServ.create(form).subscribe(res => {
                this.toListPage();
            });
        } else {
            rolesServ.updateById(form.id, form).subscribe(res => {
                this.toListPage();
            });
        }

    }
    private toListPage() {
        this.router.navigate(['roles']);
    }
}
