import { Component, OnInit } from '@angular/core';
import { RoleItem, RolesService } from '../../../services/roles.service';
@Component({
    selector: 'app-list-page',
    templateUrl: './list-page.component.html',
    styleUrls: ['./list-page.component.scss']
})
export class ListPageComponent implements OnInit {
    roleList: RoleItem[] = [];
    constructor(
        private rolesServ: RolesService
    ) {
        this.rolesServ.fetchAll().subscribe(res => this.roleList = res);
    }

    ngOnInit() {
    }
    onDeleteById(id: string | number) {
        this.rolesServ.deleteById(id).subscribe(res1 => {
            this.rolesServ.fetchAll().subscribe(res2 => this.roleList = res2);
        });
    }
}
