import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class OperatorsService {

    constructor(
        private http: HttpClient
    ) { }

    fetchAll(): Observable<OperatorsItem[]> {
        return this.http.get('/api/operators') as Observable<OperatorsItem[]>;
    }
    fetchById(id: number | string): Observable<OperatorsItem> {
        return this.http.get(`/api/operators/${id}`) as Observable<OperatorsItem>;
    }
    updateById(id: string | number, doc: OperatorsItem): Observable<any> {
        return this.http.put('/api/operators/' + id, doc);
    }
    create(doc: OperatorsItem): Observable<any> {
        return this.http.post('/api/operators', doc);
    }
    deleteById(id: number | string): Observable<any> {
        return this.http.delete('/api/operators/' + id);
    }


}


export class OperatorsItem {
    id: number;
    pid: number;
    name: string;
    value: string;
    permission_id: number;
}
