import { Component, OnInit } from '@angular/core';
import { PartnerItem, PartnersService } from '../../../services/partners.service';
import { listToTree } from 'src/app/shared/util/listToTree';

@Component({
    selector: 'app-list-page',
    templateUrl: './list-page.component.html',
    styleUrls: ['./list-page.component.scss']
})
export class ListPageComponent implements OnInit {
    partnerList: PartnerItem[] = [];
    partnerTree: PartnerTree[] = [];
    constructor(
        private partnersServ: PartnersService
    ) {
        this.partnersServ.fetchAll().subscribe(res => {
            this.partnerList = res;
            this.partnerTree = listToTree(res, null);
        });
    }

    ngOnInit() {
    }

    onDelete(id: number | string) {
        this.partnersServ.deleteById(id).subscribe(res1 => {
            this.partnersServ.fetchAll().subscribe(res2 => {
                this.partnerList = res2;
            });
        });
    }
}

type PartnerTree = PartnerItem & { children: PartnerTree[] };

