import { Component, OnInit, Input, Output, TemplateRef } from '@angular/core';
import { listToTree } from '../util/listToTree';
@Component({
    selector: 'app-tree',
    templateUrl: './tree.component.html',
    styleUrls: ['./tree.component.scss']
})
export class TreeComponent implements OnInit {
    public treeData: TreeData[] = [];
    // tslint:disable-next-line: variable-name
    private _data: InputData[] = [];
    @Input()
    set data(data: InputData[]) {
        this._data = data;
        this.treeData = listToTree(data, null);
    }
    get data() {
        return this._data;
    }
    @Input() tpl: TemplateRef<InputData> = null;

    constructor() { }

    ngOnInit() {
    }

}

interface InputData {
    id: number;
    pid: number;
    [key: string]: any;
}
interface TreeData extends InputData {
    children: TreeData[];
}

